    export default {
        name: 'app-category-list',
	    props: {
            list: {
                type: Array,
	            default() {
                    return [];
	            }
            },
            windowHeight: {
                type: Number,
	            default() {
                    return 0
	            }
            },
            windowWidth: {
                type: Number,
                default() {
                    return 0
                }
            },
            botHeight: {
                type: Number,
                default() {
                    return 0
                }
            },
            noSetHeight: {
                type: String
            },
			theme: Object
	    },
	    methods: {
            active(item, index) {
                this.$emit('click', {
                    item, index
                });
            }
	    },
	    computed: {
		    setHeight() {
                let bottom = 0;
                if (this.$parent.$parent.$children[0].tabbarbool) {
                    bottom = this.botHeight;
                }
                return (this.windowHeight * (750 / this.windowWidth)) - bottom - 88 + 'rpx';
		    }
	    }
    }
